import validator from "validator";

export default class Login {
    constructor(formClass) {
        this.form = document.querySelector(formClass);
    }

    init() {
        this.events();
    }

    events() {
        if (!this.form) return;
        this.form.addEventListener("submit", (e) => {
            e.preventDefault();
            this.validate();
        });
    }

    validate() {
        const emailInput = this.form.querySelector('input[name="email"]');
        const passwordInput = this.form.querySelector('input[name="password"]');
        let error = false;

        // Validao do e-mail
        if (!validator.isEmail(emailInput.value)) {
            alert("E-mail invlido");
            error = true;
        }

        // Validao da senha
        if (passwordInput.value.length < 3 || passwordInput.value.length > 50) {
            alert("Senha invlida. A senha deve ter entre 3 e 50 caracteres.");
            error = true;
        }

        // Caso no haja erros, enviar o formulrio ou executar outra ao
        if (!error) {
            console.log("Formulrio vlido!");
            console.log("E-mail:", emailInput.value, "Senha:", passwordInput.value);
            this.form.submit(); // Caso deseje submeter o formulrio
        }
    }
}
